import divider from '@/components/divider.vue';
import uniIcon from '@/components/uni-icon/uni-icon.vue'
export default {
	components:{
		divider,
		uniIcon
	},
	data() {
		return {
			background:{
				backgroundColor: '#fff',
			},
			showSelect:false,
			params: {
				year: true,
				month: true,
				day: true,
				hour: true,
				minute: true,
				second: false
			},
			chooseIndex:-1,
			startTime:"选择开始时间",
			endTime:"选择结束时间",
			selectType:"选择指定条件",
			show: false,
			num:1,
			catagor: [{
					name: '全部订单',
					id:1
				}, {
					name: '门店优惠订单',
					id:2
				},
				{
					name: '到店核销订单',
					id:3
				},
				{
					name: '活动线上订单',
					id:4
				},
				{
					name: '活动到店买单',
					id:5
				},
				{
					name: '余额充值',
					id:6
				},
				{
					name: '余额核销',
					id:7
				},
				{
					name: '商城订单',
					id:8
				}
			],
		}
	},
	
	methods: {
		selectTime(index){
			this.show = true
			if(index==1){
				this.num=1
			}
			else{
				this.num=2
			}
		},
		// 选择时间
		chooseTime(res) {
			if(this.num==1){
				this.startTime = res.year+"年" +res.month + "月" + res.day + "日"+res.hour+"时"+res.minute+"分";
			}
			else{
				this.endTime = res.year+"年" +res.month + "月" + res.day + "日"+res.hour+"时"+res.minute+"分";
			}
			
		},
		
		// 显示条件弹窗
		selectFilter(){
			this.showSelect=true
		},
		
		// 选择条件
		chooseItem(num){
			this.chooseIndex=num;
		},
		
		
		cancle(){
			this.showSelect=false  
		},
		
		confirm(){
			this.showSelect=false  
			this.selectType=this.catagor[this.chooseIndex].name
		}
	
	}
}
